
import { getDataBanks, hideModal, ShowDataModal, showModal } from "../../../actions/datamaster_action";
import {
    React,
    connect,
    Link,
    reduxForm,
    Panel,
    PanelHeader,
    PanelBody,
    Tabel,
    ModalGlobal,
    ToastNotification,
    reset,
    postDataNoTImeOut,
    putDataNoTImeOut,
    deleteData,
    Swal
  } from "../../../components/helpers/library";
import HeadMasterBank from "./HeadMasterBank";
  
  const maptostate = (state) => {
    return {
      data : state.datamaster.getDataBanks
    }
  }
  class MasterBank extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isLoading: false,
        settings : "",
        columns: [
            {
              dataField: "kode_bank",
              text: "Kode Bank",
              sort: true,
            },
            {
              dataField: "nama_bank",
              text: "Nama Bank",
            },
            {
              dataField: "action",
              text: "Action",
              csvExport: false,
              headerClasses: "text-center",
              formatter: (rowcontent, row) => {
                let dataEdit = {
                  id: row._id,
                  kode_bank: row.kode_bank,
                  nama_bank: row.nama_bank,
                };
                 
                return (
                  <div className="row text-center">
                    <div className="col-12">
                      <button
                        onClick={() => this.modalEdit(dataEdit)}
                        className="btn btn-primary mr-3"
                      >
                        <i className="fa fa-edit"></i>
                      </button>
                      <button
                        onClick={() =>
                          this.hapusData(row)
                        }
                        className="btn btn-danger"
                      >
                        <i className="fa fa-trash"></i>
                      </button>
                    </div>
                  </div>
                );
              },
            },
          ],
      };
      this.handleSubmit = this.handleSubmit.bind(this);
      // this.print = this.print.bind(this);
    }
  
    handleSubmit(data) {
      this.setState({
        isLoading: true,
      });
      this.state.isEdit ?
      putDataNoTImeOut('banks/'+data.id,data).then((res)=>{
        ToastNotification("success",res?.data?.message || "Berhasil Menyimpan Data")
        this.setState({
          isLoading: false,
        });
        this.props.dispatch(hideModal());
        this.props.dispatch(getDataBanks())
      }).catch((err)=>{
        this.setState({
          isLoading: false,
        });
        ToastNotification("info",err.response?.data?.message || "Terjadi Kesalhaan Saat Mengirim Data")
      })
      :
      postDataNoTImeOut('banks',data).then((res)=>{
        ToastNotification("success",res?.data?.message || "Berhasil Menyimpan Data")
        this.setState({
          isLoading: false,
        });
        this.props.dispatch(hideModal());
        this.props.dispatch(getDataBanks())
        this.props.dispatch(reset());
      }).catch((err)=>{
        this.setState({
          isLoading: false,
        });
        ToastNotification("info",err.response?.data?.message || "Terjadi Kesalhaan Saat Mengirim Data")
      })
    }
  
    componentDidMount() {
     this.props.dispatch(getDataBanks())
    }
    hapusData(row){
      Swal.fire({
        // title: "Anda Yakin !!",
        // text: "Ingin Menghapus Data Ini ?",
        html:
          "Apakah Anda Yakin Ingin " +
          "Menghapus " +
          "<h1><b>Kode Bank " +
          row.kode_bank +
          "</b> ?</h1>",
        icon: "warning",
        position: "top-center",
        cancelButtonText: "Tidak",
        showCancelButton: true,
        confirmButtonText: "OK",
        showConfirmButton: true,
      }).then((result) => {
        if (result.isConfirmed) {
          deleteData('banks/'+row._id).then((res)=>{
            ToastNotification('success',res.data.message || "Berhasil Menghapus Data")
            this.setState({
              isLoading: false,
            });
            this.props.dispatch(getDataBanks())
          }).catch((err)=>{
            ToastNotification('info',err.response?.data?.message || "Gagal Menghapus Data")
            this.setState({
              isLoading: false,
            });
          })
        }
      });
    }
    modalEdit(row){
      this.props.dispatch(showModal());
      this.props.dispatch(ShowDataModal(row));
      this.setState({
        isEdit: true,
      });
    }
    showModal(){
        this.props.dispatch(showModal());
        this.props.dispatch(ShowDataModal(false));
        this.setState({
          isEdit: false,
        });
    }
    render() {
      return (
        <div>
          <ol className="breadcrumb float-xl-right">
            <li className="breadcrumb-item">
              <Link to="#">Data Master</Link>
            </li>
            <li className="breadcrumb-item active">Master Bank</li>
          </ol>
          <h1 className="page-header">Master Bank </h1>
          <Panel>
            <PanelHeader>Master Bank</PanelHeader>
            <PanelBody>
            <Tabel
              keyField="kode_bank"
              tambahData={true}
              handleClick={() => this.showModal()}
              exportCsv={true}
              data={this.props.data}
              columns={this.state.columns}
              empty={[]}
              textEmpty="Data Bank"
            />
            </PanelBody>
          </Panel>
          <ModalGlobal
            title={this.state.isEdit ? "Edit Data Bank" : "Tambah Data Bank"}
            size="P"
            content={
              <HeadMasterBank
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />
        </div>
      );
    }
  }
  
  MasterBank = reduxForm({
    form: "MasterBank",
    enableReinitialize: true,
  })(MasterBank);
  export default connect(maptostate)(MasterBank);