const ValidasiMasterSales = (value, event) => {
  const errors = {};
  // console.log(event.charCode==13);
  if (!value.kode_sales) {
    errors.kode_sales = "Kode Sales Tidak Boleh Kosong";
  }
  if (!value.nama_sales) {
    errors.nama_sales = "Nama Sales Tidak Boleh Kosong";
  }
  if (!value.kode_bank) {
    errors.kode_bank = "Kode Bank Tidak Boleh Kosong";
  }
  if (!value.nama_bank) {
    errors.nama_bank = "Nama Bank Tidak Boleh Kosong";
  }
  if (!value.no_rekening) {
    errors.no_rekening = "No Rekening Tidak Boleh Kosong";
  }
  if (!value.nama_rekening) {
    errors.nama_rekening = "Nama Rekening Tidak Boleh Kosong";
  }
  return errors;
};

export default ValidasiMasterSales;
